/* eslint-disable */
import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

import { m } from "m-labs";

createApp(App).use(store).use(router).use(m).mount("#app");

import * as THREE from "three";
import { GLTFLoader } from "./jsm/loaders/GLTFLoader";
// import Stats from "./jsm/libs/stats.module.js";
// import { GUI } from "./jsm/libs/lil-gui.module.min.js";
// import { OrbitControls } from "./jsm/controls/OrbitControls.js";

/**
 * Debug
 */
const parameters = {
  materialColor: "#ffeded",
};

/**
 * Base
 */

// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();

/**
 * Objects
 */

// Texture
const textureLoader = new THREE.TextureLoader();
const gradientTexture = textureLoader.load("./textures/gradients/3.jpg");
gradientTexture.magFilter = THREE.NearestFilter;

// Material
const material = new THREE.MeshToonMaterial({
  color: parameters.materialColor,
  gradientMap: gradientTexture,
});

/**
 * Sizes
 */
const sizes = {
  width: window.innerWidth,
  height: window.innerHeight,
};

// Base camera
const camera = new THREE.PerspectiveCamera(
  35,
  sizes.width / sizes.height,
  0.1,
  100
);
camera.position.z = 9;
camera.position.x = 0;
camera.position.y = -0.5;
// camera.lookAt( camTarget );
scene.add(camera);

const loader = new GLTFLoader();
let gamma;
if (sizes.width < 900) {
  gamma = 0.1;
  camera.position.y = 1.5;
  camera.position.x = 0.5;

  scene.add(camera);
} else {
  gamma = 0.15;
}

loader.load("/components/back.glb", function async(gltf) {
  let obj = gltf.scene;
  obj.scale.set(gamma, gamma, gamma);

  for (let index = 0; index < obj.children.length; index++) {
    obj.children[index].clone();
    obj.children[index].material.metalness = -1.5;
    obj.children[index].position.x -= 30;
    oldPosZ = obj.children[index].position.z - 2;

    if (sizes.width < 900) {
      obj.children[index].position.x += 3;
    }

    obj.children[index].position.z = (Math.random() - 0.5) * 10 + 50;
  }
  obj.traverse(function (child) {
    if (child instanceof THREE.Mesh) child.material.color.setHex(0x49299c);
  });
  obj.name = "back";
  scene.add(obj);
});

loader.load("/components/logo_cube.glb", function async(gltf) {
  let obj = gltf.scene;
  obj.scale.set(gamma, gamma, gamma);
  for (let index = 0; index < obj.children.length; index++) {
    obj.children[index].clone();
    obj.children[index].material.metalness = -1;
    obj.children[index].position.x -= 16;
    if (sizes.width < 900) {
      obj.children[index].position.x += 4;
      obj.children[index].position.y += 19;
    }
    oldPosZ = obj.children[index].position.z - 2;
    obj.children[index].position.z = (Math.random() - 0.5) * 10 + 100;
  }
  obj.name = "logo";
  scene.add(obj);
});
let oldPosZ = 0;
let companyName;

/**
 * Lights
 */

const light = new THREE.HemisphereLight(0xffffbb, 0x080820, 1);
scene.add(light);

window.addEventListener("resize", () => {
  // Update sizes
  sizes.width = window.innerWidth;
  sizes.height = window.innerHeight;

  // Update camera
  camera.aspect = sizes.width / sizes.height;
  camera.updateProjectionMatrix();

  // Update renderer
  renderer.setSize(sizes.width, sizes.height);
  renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
});

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  alpha: true,
});

if (sizes.width < 800) camera.aspect = sizes.width / sizes.height + 0.7;
camera.updateProjectionMatrix();

renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

/**
 * Scroll
 */
let currentSection = 0;

/**
 * Cursor
 */
const cursor = {};
cursor.x = 0;
cursor.y = 0;

/**
 * Animate
 */
let changed = false;
const clock = new THREE.Clock();
let previousTime = 0;
let runCount = 0;
const tick = () => {
  runCount = runCount + 1;
  if (runCount >= 1) {
    runCount = 0;
    for (let index = 0; index < scene.children.length; index++) {
      if (scene.children[index].name == "back") {
        for (let m = 0; m < scene.children[index].children.length; m++) {
          if (
            Math.abs(scene.children[index].children[m].position.z - 3.1) > 1.5
          ) {
            if (scene.children[index].children[m].position.z < 0)
              scene.children[index].children[m].position.z +=
                Math.random() + 0.3;
            else {
              scene.children[index].children[m].position.z -=
                Math.random() + 0.3;
            }
          }
        }
      } else {
        changed = true;
        for (let s = 0; s < scene.children.length; s++) {
          if (scene.children[s].name == "logo") {
            for (let m = 0; m < scene.children[s].children.length; m++) {
              if (
                Math.abs(scene.children[s].children[m].position.z - 3.1) > 2.5
              ) {
                if (scene.children[s].children[m].position.z < 0)
                  scene.children[s].children[m].position.z += Math.random();
                else {
                  scene.children[s].children[m].position.z -= Math.random();
                }
              }
            }
          }
        }
      }
    }
  }

  // Render
  renderer.render(scene, camera);
  window.requestAnimationFrame(tick);
};

tick();
